.App {
  text-align: center;
}

body {    background-color: #f0f0f0;
  padding: 10px;

}
.main{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: system-ui,-apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Oxygen,Ubuntu,Cantarell,Open Sans,Helvetica Neue,sans-serif;
}
section#todoList {
  width: 90%;
  max-width: 600px;
  margin: 20px auto;
  background-color: #fff;
  padding: 20px;
  border-radius: 10px;
}

p {
  display: block;
  margin-block-start: 1em;
  margin-block-end: 1em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: system-ui,-apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Oxygen,Ubuntu,Cantarell,Open Sans,Helvetica Neue,sans-serif;
}

section#todoList .input input {
  width: 100%;
  border: none;
  padding: 5px 10px;
  background: #e0e0e0;
  border-radius: 5px;
}

input {

 
  text-rendering: auto;
  color: fieldtext;
  letter-spacing: normal;
  word-spacing: normal;
  line-height: normal;
  text-transform: none;
  text-indent: 0px;
  text-shadow: none;
  display: inline-block;
  text-align: start;
  appearance: auto;
  -webkit-rtl-ordering: logical;
  cursor: text;
  background-color: field;
  margin: 0em;
  padding: 1px 2px;
  border-width: 2px;
  border-style: inset;
  border-color: -internal-light-dark(rgb(118, 118, 118), rgb(133, 133, 133));
  border-image: initial;
  height: 7vh;
}

section#todoList ul li button {
  padding: 4px;
  display: flex;
  align-items: center;
  background-color: transparent;
  margin-left: auto;
  border: none;
  font-size: 20px;
  cursor: pointer;
  color: #ff00008c;
}
section#todoList ul {
  list-style-type: none;
  padding: 10px 5px;
}

input[type="checkbox" i] {
  background-color: initial;
  cursor: default;
  appearance: auto;
  box-sizing: border-box;
  margin: 3px 3px 3px 4px;
  padding: initial;
  border: initial;
}

.completed{
  margin-right: 30px;
  
}